<template lang="pug">
.container
  article
    h1 Bienvenue sur le site de survie des confinés.

    p Afin de conserver des liens humains et amicaux durant cette période difficile, ce logiciel permet de se retrouver ensemble autour d'un visio-apéro. Pour participer, c'est très simple:

    h3 Vérifier avant tout que vous venez au bon moment: <a href="https://estcequecestbientotlapero.fr/" target="_blank">Cliquer ici pour vérifier</a>.
    p C'est le cas ? Alors
    h3 Appuyer sur ce bouton pour être redirigé aléatoirement sur un des {{ aperooms.length }} apérooms ouverts:
    p
      button(@click="redirectToRoom()") Envoie-moi vers un apéroom !
    p.logo A service
      img(src='../assets/by-linagora.svg' width='100px')
</template>

<script>
export default {
  data() {
    return {
      aperooms: [
        'https://jitsi.linagora.com/aperoom1',
        'https://jitsi.linagora.com/aperoom2',
        'https://jitsi.linagora.com/aperoom3',
        'https://jitsi.linagora.com/aperoom4'
      ]
    };
  },
  methods: {
    redirectToRoom() {
      const roomUrl = this.aperooms[getRandomInt(0, this.aperooms.length - 1)];
      window.location = roomUrl;
    }
  }
};

function getRandomInt(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min + 1)) + min;
}
</script>
